/* import CSS style sheets */
@import url('./reset.css');
@import url('./base.css');

/************************************************************ 
MAIN STYLES 
*************************************************************/
/* container.wrapper */
.wrapper {
  width: 80vw;
  margin: 0 auto 1em;
  text-align: center;
}

.grid-container {
  display: -ms-grid;
  display: grid;
  grid-gap: 1.5em;
  -ms-flex-pack: center;
      justify-content: center;
  grid-template-columns: minmax(150px, 350px);
}

/* card  */
.card {
  border-radius: 0.6em;
  text-align: center;
  
  -webkit-box-shadow: 0 7px 0 var(--lead-dark);
  
          box-shadow: 0 7px 0 var(--lead-dark);
  -webkit-transition: all 450ms ease;
  -o-transition: all 450ms ease;
  transition: all 450ms ease;
}
.card:hover {
  -webkit-box-shadow: 0 0 1px var(--lead-dark);
          box-shadow: 0 0 1px var(--lead-dark);
  -webkit-transform: translateY(7px);
      -ms-transform: translateY(7px);
          transform: translateY(7px);
}
.card:hover .card-image_container {
  -webkit-box-shadow: inset 0 1px 0 var(--lead);
          box-shadow: inset 0 1px 0 var(--lead);
}

.card-image_container {
  border-radius: 0.6em 0.6em 0 0;
  /* background-color: var(--lead-slightly-light); */
  background: -webkit-gradient(linear,
                              left top, left bottom,
                              color-stop(45%, var(--lead-light)),
                              color-stop(0, var(--lead-slightly-light)), color-stop(55%, var(--lead-slightly-light)),
                              color-stop(0, var(--lead-light)));
  background: -webkit-linear-gradient(var(--lead-light) 45%,
                              var(--lead-slightly-light) 0, var(--lead-slightly-light) 55%,
                              var(--lead-light) 0);
  background: -o-linear-gradient(var(--lead-light) 45%,
                              var(--lead-slightly-light) 0, var(--lead-slightly-light) 55%,
                              var(--lead-light) 0);
  background: linear-gradient(var(--lead-light) 45%,
                              var(--lead-slightly-light) 0, var(--lead-slightly-light) 55%,
                              var(--lead-light) 0);
  padding: 2em 0;
  -webkit-transition: -webkit-box-shadow 450ms ease;
  transition: -webkit-box-shadow 450ms ease;
  -o-transition: box-shadow 450ms ease;
  transition: box-shadow 450ms ease;
  transition: box-shadow 450ms ease, -webkit-box-shadow 450ms ease;
}

.card > p {
  position: relative;
  border-radius: 0 0 0.6em 0.6em;
  background-color: var(--lead);
  border-top: 2px dashed rgba(51, 51, 51, 0.2);
  padding: 1em;
  padding-top: 1.8em;
}
.card h3 {
  position: absolute;
  top: -1em;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);

  background-color: var(--nightfall);
  border-radius: 2px;
  color: white;
  text-shadow: 0 1.5px 0 var(--black);
  text-transform: uppercase;
  padding: 0.3em 0.8em;
}


/* MEDIA QUERIES */
@media (min-width: 750px) {
  .grid-container {
    grid-template-columns: repeat(2, minmax(150px, 350px)); 
  }
}

@media (min-width: 1200px) {
  .grid-container {
    grid-template-columns: repeat(3, minmax(150px, 350px)); 
  }
}


